import React from 'react'
import '../carrouselVerti.css'
import bcpcloud from '../images/backupcloud.png'
import mnt from '../images/maintenance.png'
import dispo from '../images/24-support-team.png'

function CarrouselVerti() {
    
    return(
        <div id="carouselExampleSlidesOnly" class="carousel-slide" data-ride="carousel">
            <img src={bcpcloud} className="d-block" alt="Backup Cloud"></img>
            <img src={mnt} class="d-block" alt="..."></img>
            <img src={dispo} class="d-block" alt="..."></img>
        </div>
    )
}


export default CarrouselVerti